main section {
  position: relative;
  left: 0;
  /* top: var(--nav-height); */
  z-index: 0;
}

.fade-out-switch-exit {
  position: absolute;
  transform: scale(1);
  left: 0%;
}

.fade-out-switch-exit-active {
  transform: scale(0.75);
  left: -200%;
  transition: transform 250ms, left 500ms 250ms;
}

.fade-out-switch-enter {
  position: absolute;
  left: 200%;
  transform: scale(0.75);
}

.fade-out-switch-enter-active {
  left: 0%;
  transform: scale(1);
  transition: left 500ms 250ms, transform 250ms 750ms;
}
