.navbar {
  position: relative;
  background: #000;
  height: var(--nav-height);
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--button-active);
  box-shadow: 0 2px 10px var(--button-active);
  overflow: hidden;
  z-index: 1;
}

.text-logo h4 {
  margin-left: 4vw;
  letter-spacing: 4px;
  color: var(--font-color);
  text-transform: uppercase;
}

.nav-items {
  display: flex;
  margin-left: auto;
  margin-right: 4vw;
  line-height: var(--nav-height);
}

.nav-items div {
  background: transparent;
  color: var(--font-color);
  padding: 0 2vw;
  height: 100%;
  display: block;
  transition: 0.5s;
  letter-spacing: 2px;
  border: none;
  cursor: pointer;
}

.nav-items div:hover {
  background: var(--button-highlight);
  box-shadow: 0 0 20px var(--button-highlight);
}

.nav-items .nav-active {
  background: var(--button-active);
  box-shadow: 0 0 20px var(--button-active);
  color: var(--button-highlight);
  pointer-events: none;
}

.nav-items .nav-active:hover {
  background: var(--button-active);
  box-shadow: 0 0 20px var(--button-active);
  color: var(--button-highlight);
}
