@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@100;400&display=swap);
:root {
  --nav-height: 40px;
  --button-highlight: #4895ef;
  --button-default: transparent;
  --button-active: #3f37c9;
  --font-color: white;
  --font-highlight: #f72585;
  --font-alternative: #4895EF;
}
* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;

  /* For firefox only */
  scrollbar-color: #3f37c9 transparent;
  scrollbar-color: var(--button-active) transparent;
  scrollbar-width: thin;
}

*::-webkit-scrollbar-track {
  background-color: transparent;
}

*::-webkit-scrollbar {
  width: 3px;
  height: 5px;
  background-color: transparent;
}

*::-webkit-scrollbar-thumb {
  background-color: #3f37c9;
  background-color: var(--button-active);
}

*::-webkit-scrollbar-corner {
  background-color: transparent;
}

body {
  background: black;
  font-family: 'Roboto', Arial, Helvetica, sans-serif;
  overflow-y: hidden;
}

main section {
  position: relative;
  left: 0;
  /* top: var(--nav-height); */
  z-index: 0;
}

.fade-out-switch-exit {
  position: absolute;
  transform: scale(1);
  left: 0%;
}

.fade-out-switch-exit-active {
  transform: scale(0.75);
  left: -200%;
  transition: transform 250ms, left 500ms 250ms;
}

.fade-out-switch-enter {
  position: absolute;
  left: 200%;
  transform: scale(0.75);
}

.fade-out-switch-enter-active {
  left: 0%;
  transform: scale(1);
  transition: left 500ms 250ms, transform 250ms 750ms;
}

.navbar {
  position: relative;
  background: #000;
  height: var(--nav-height);
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--button-active);
  box-shadow: 0 2px 10px var(--button-active);
  overflow: hidden;
  z-index: 1;
}

.text-logo h4 {
  margin-left: 4vw;
  letter-spacing: 4px;
  color: var(--font-color);
  text-transform: uppercase;
}

.nav-items {
  display: flex;
  margin-left: auto;
  margin-right: 4vw;
  line-height: var(--nav-height);
}

.nav-items div {
  background: transparent;
  color: var(--font-color);
  padding: 0 2vw;
  height: 100%;
  display: block;
  transition: 0.5s;
  letter-spacing: 2px;
  border: none;
  cursor: pointer;
}

.nav-items div:hover {
  background: var(--button-highlight);
  box-shadow: 0 0 20px var(--button-highlight);
}

.nav-items .nav-active {
  background: var(--button-active);
  box-shadow: 0 0 20px var(--button-active);
  color: var(--button-highlight);
  pointer-events: none;
}

.nav-items .nav-active:hover {
  background: var(--button-active);
  box-shadow: 0 0 20px var(--button-active);
  color: var(--button-highlight);
}

.landingSection {
  overflow: hidden;
}

.p5js {
  position: absolute;
  width: 100vw;
  height: calc(100vh - var(--nav-height) - 1px);
  z-index: -1;
  overflow: hidden;
}

.summary-container {
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
  width: 100%;
  max-width: 100vw;
  height: calc(100vh - var(--nav-height) - 1px);
  overflow: scroll;
  background-color: rgba(0, 0, 0, 0.5);
}

.summary-container .img-container {
  height: 300px;
  width: auto;
  margin-bottom: 30px;
  margin-top: 10vh;
}

.summary-container .img-container img {
  width: auto;
  height: 100%;
  border: 1px solid var(--font-highlight);
  box-shadow: 0 0px 30px var(--font-highlight);
  border-radius: 100px;
  object-fit: cover;
  object-position: center;
}

.summary-container .text-container {
  text-align: justify;
  width: 75%;
}

.text-container h3 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.5rem;
  color: var(--font-highlight);
  text-shadow: 0px 0px 20px var(--font-highlight);
  margin: 20px 0;
  text-align: center;
}

.text-container h4 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.25rem;
  color: var(--font-alternative);
  text-shadow: 0px 0px 20px var(--font-alternative);
  margin: 0px 0px -15px 20px;
  text-align: left;
}

.text-container h5 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1rem;
  color: var(--font-alternative);
  text-shadow: 0px 0px 20px var(--font-alternative);
  margin: -5px 0px -15px 0px;
  text-align: left;
}

.text-container p {
  letter-spacing: 1px;
  font-size: 1rem;
  color: var(--font-color);
  margin: 20px 0;
}

.text-container p span {
  color: #4CC9F0;
  text-shadow: 0px 0px 5px #4CC9F0;
}

.text-container .highlight {
  color: var(--font-highlight);
  text-shadow: 0px 0px 5px var(--font-highlight);
}

.text-container .summary-footer {
  margin-top: 30px;
  margin-bottom: 20vh;
  text-align: center;
}

.text-container .summary-footer p {
  margin: 0;
  font-size: 1.1rem;
}

.sectionClass .p5js {
  width: 100vw;
  height: calc(100vh - var(--nav-height) * 2);
  z-index: -1;
}
.controls {
  position: absolute;
  display: flex;
  align-items: flex-end;
  background-color: transparent;
  top: calc(50vh - var(--nav-height));
  height: 50vh;
  width: 100vw;
  overflow-x: auto;
  overflow-y: hidden;
}

.controls .footer-container {
  position: relative;
  display: flex;
  justify-content: baseline;
  align-items: center;
  background-color: black;
  color: white;
  height: var(--nav-height);
  width: 1000%;
  border-top: 1px solid var(--button-active);
  box-shadow: 0 -2px 10px var(--button-active);
  white-space: nowrap;
}

.controls .hintspan {
  margin: 0px 20px;
}

/* buttons only */
.clickable-button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  border: none;
  background-color: transparent;
  color: white;
  outline: none;
  cursor: pointer;
  height: 80%;
  width: 150px;
  display: flex;
  align-items: center;
  margin: 0px 20px;
}

.clickable-button span {
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1 1;
  background-color: transparent;
}

.clickable-button span:hover {
  background-color: var(--button-highlight);
}

.clickable-button .button-start {
  border-left: 2px solid var(--button-highlight);
  border-top: 2px solid var(--button-highlight);
  border-bottom: 2px solid var(--button-highlight);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.clickable-button .button-middle {
  border-top: 2px solid var(--button-highlight);
  border-bottom: 2px solid var(--button-highlight);
}

.clickable-button .button-end {
  border-right: 2px solid var(--button-highlight);
  border-top: 2px solid var(--button-highlight);
  border-bottom: 2px solid var(--button-highlight);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

/* Checkbox only */
.chk-container {
  font-size: 0.8rem;
  height: 100%;
  width: 150px;
  letter-spacing: 1.5px;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
  cursor: pointer;
}

.chk-container:hover {
  background-color: var(--button-active);
  box-shadow: 0 0 10px var(--button-active);
}

.chk-container label {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
}

.chk-container input {
  outline: none;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: #000;
  border: 2px solid var(--button-highlight);
  height: 20px;
  width: 20px;
  border-radius: 10px;
  cursor: pointer;
}

.chk-container input:checked {
  box-shadow: 0px 0px 10px 5px var(--button-highlight) inset;
}

/* Dropdown only */
.dropdown {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-width: 200px;
  padding: 0px 20px;
  letter-spacing: 1.5px;
  font-size: 0.8rem;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;

  transition: 0.5s;
}

.dropdown .dropdown-content {
  position: absolute;
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.9);
  min-width: 200px;
  height: 0px;
  overflow-y: auto;
  z-index: 1;
  bottom: 100%;
  border: 2px solid var(--button-active);
  box-shadow: 0px 0px 10px 2px var(--button-active);
  opacity: 0;

  transition: 0.5s;
}

.dropdown:hover {
  background-color: var(--button-active);
  box-shadow: 0 0 10px var(--button-active);
}

.dropdown:hover .dropdown-content {
  opacity: 1;
  height: var(--prefHeight);
}

/* Drop down children */
/* ul */
.dropdown .dropdown-content ul {
  list-style: none;
  width: 100%;
}

.dropdown .dropdown-content li {
  cursor: pointer;
  width: 100%;
  text-align: center;
  padding: 15px 0px;
  transition: 0.25s;
}

.dropdown .dropdown-content li:hover {
  background-color: var(--button-active);
  box-shadow: 0 0 10px var(--button-active);
}

.dropdown .dropdown-content .active {
  background-color: var(--button-highlight);
  box-shadow: 0 0 10px var(--button-highlight);
}

.dropdown .dropdown-content .active:hover {
  background-color: var(--button-highlight);
  box-shadow: 0 0 10px var(--button-highlight);
}

/* range */
input[type='range'] {
  -webkit-appearance: none;
  width: 100%;
  background: transparent;
  margin: 0px 20px;
}

input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  height: 12px;
  width: 12px;
  border-radius: 100%;
  border: 2px solid var(--button-highlight);
  background-color: var(--button-highlight);
  box-shadow: 0px 0px 20px 5px var(--button-highlight), 0px 0px 10px 2px var(--button-highlight);

  /* Corrects alignment isssue with chrome and edge */
  margin: -6px 0px;
}

input[type='range']::-moz-range-thumb {
  cursor: pointer;
  height: 10px;
  width: 10px;
  border-radius: 100%;
  border: 2px solid var(--button-highlight);
  background-color: var(--button-highlight);
  box-shadow: 0px 0px 20px 5px var(--button-highlight), 0px 0px 10px 2px var(--button-highlight);
}

input[type='range']::-moz-range-track {
  width: 100%;
  height: 2px;
  background: var(--font-highlight);
  box-shadow: 0px 0px 10px 2px var(--font-highlight);
  border-radius: 100%;
}

input[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  background: var(--font-highlight);
  box-shadow: 0px 0px 10px 2px var(--font-highlight);
  border-radius: 100%;
}

input[type='range']:focus {
  outline: none;
}

.sectionClass .p5js {
  width: 100vw;
  height: calc(100vh - var(--nav-height) * 2);
  z-index: -1;
}
