.landingSection {
  overflow: hidden;
}

.p5js {
  position: absolute;
  width: 100vw;
  height: calc(100vh - var(--nav-height) - 1px);
  z-index: -1;
  overflow: hidden;
}

.summary-container {
  display: flex;
  flex-direction: column;
  justify-content: baseline;
  align-items: center;
  width: 100%;
  max-width: 100vw;
  height: calc(100vh - var(--nav-height) - 1px);
  overflow: scroll;
  background-color: rgba(0, 0, 0, 0.5);
}

.summary-container .img-container {
  height: 300px;
  width: auto;
  margin-bottom: 30px;
  margin-top: 10vh;
}

.summary-container .img-container img {
  width: auto;
  height: 100%;
  border: 1px solid var(--font-highlight);
  box-shadow: 0 0px 30px var(--font-highlight);
  border-radius: 100px;
  object-fit: cover;
  object-position: center;
}

.summary-container .text-container {
  text-align: justify;
  width: 75%;
}

.text-container h3 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.5rem;
  color: var(--font-highlight);
  text-shadow: 0px 0px 20px var(--font-highlight);
  margin: 20px 0;
  text-align: center;
}

.text-container h4 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1.25rem;
  color: var(--font-alternative);
  text-shadow: 0px 0px 20px var(--font-alternative);
  margin: 0px 0px -15px 20px;
  text-align: left;
}

.text-container h5 {
  text-transform: uppercase;
  letter-spacing: 2px;
  font-size: 1rem;
  color: var(--font-alternative);
  text-shadow: 0px 0px 20px var(--font-alternative);
  margin: -5px 0px -15px 0px;
  text-align: left;
}

.text-container p {
  letter-spacing: 1px;
  font-size: 1rem;
  color: var(--font-color);
  margin: 20px 0;
}

.text-container p span {
  color: #4CC9F0;
  text-shadow: 0px 0px 5px #4CC9F0;
}

.text-container .highlight {
  color: var(--font-highlight);
  text-shadow: 0px 0px 5px var(--font-highlight);
}

.text-container .summary-footer {
  margin-top: 30px;
  margin-bottom: 20vh;
  text-align: center;
}

.text-container .summary-footer p {
  margin: 0;
  font-size: 1.1rem;
}
