.controls {
  position: absolute;
  display: flex;
  align-items: flex-end;
  background-color: transparent;
  top: calc(50vh - var(--nav-height));
  height: 50vh;
  width: 100vw;
  overflow-x: auto;
  overflow-y: hidden;
}

.controls .footer-container {
  position: relative;
  display: flex;
  justify-content: baseline;
  align-items: center;
  background-color: black;
  color: white;
  height: var(--nav-height);
  width: 1000%;
  border-top: 1px solid var(--button-active);
  box-shadow: 0 -2px 10px var(--button-active);
  white-space: nowrap;
}

.controls .hintspan {
  margin: 0px 20px;
}

/* buttons only */
.clickable-button {
  appearance: none;
  border: none;
  background-color: transparent;
  color: white;
  outline: none;
  cursor: pointer;
  height: 80%;
  width: 150px;
  display: flex;
  align-items: center;
  margin: 0px 20px;
}

.clickable-button span {
  height: 90%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 1;
  background-color: transparent;
}

.clickable-button span:hover {
  background-color: var(--button-highlight);
}

.clickable-button .button-start {
  border-left: 2px solid var(--button-highlight);
  border-top: 2px solid var(--button-highlight);
  border-bottom: 2px solid var(--button-highlight);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.clickable-button .button-middle {
  border-top: 2px solid var(--button-highlight);
  border-bottom: 2px solid var(--button-highlight);
}

.clickable-button .button-end {
  border-right: 2px solid var(--button-highlight);
  border-top: 2px solid var(--button-highlight);
  border-bottom: 2px solid var(--button-highlight);
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

/* Checkbox only */
.chk-container {
  font-size: 0.8rem;
  height: 100%;
  width: 150px;
  letter-spacing: 1.5px;
  user-select: none;
  cursor: pointer;
}

.chk-container:hover {
  background-color: var(--button-active);
  box-shadow: 0 0 10px var(--button-active);
}

.chk-container label {
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 100%;
}

.chk-container input {
  outline: none;
  appearance: none;
  background-color: #000;
  border: 2px solid var(--button-highlight);
  height: 20px;
  width: 20px;
  border-radius: 10px;
  cursor: pointer;
}

.chk-container input:checked {
  box-shadow: 0px 0px 10px 5px var(--button-highlight) inset;
}

/* Dropdown only */
.dropdown {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-width: 200px;
  padding: 0px 20px;
  letter-spacing: 1.5px;
  font-size: 0.8rem;
  user-select: none;

  transition: 0.5s;
}

.dropdown .dropdown-content {
  position: absolute;
  display: flex;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.9);
  min-width: 200px;
  height: 0px;
  overflow-y: auto;
  z-index: 1;
  bottom: 100%;
  border: 2px solid var(--button-active);
  box-shadow: 0px 0px 10px 2px var(--button-active);
  opacity: 0;

  transition: 0.5s;
}

.dropdown:hover {
  background-color: var(--button-active);
  box-shadow: 0 0 10px var(--button-active);
}

.dropdown:hover .dropdown-content {
  opacity: 1;
  height: var(--prefHeight);
}

/* Drop down children */
/* ul */
.dropdown .dropdown-content ul {
  list-style: none;
  width: 100%;
}

.dropdown .dropdown-content li {
  cursor: pointer;
  width: 100%;
  text-align: center;
  padding: 15px 0px;
  transition: 0.25s;
}

.dropdown .dropdown-content li:hover {
  background-color: var(--button-active);
  box-shadow: 0 0 10px var(--button-active);
}

.dropdown .dropdown-content .active {
  background-color: var(--button-highlight);
  box-shadow: 0 0 10px var(--button-highlight);
}

.dropdown .dropdown-content .active:hover {
  background-color: var(--button-highlight);
  box-shadow: 0 0 10px var(--button-highlight);
}

/* range */
input[type='range'] {
  -webkit-appearance: none;
  width: 100%;
  background: transparent;
  margin: 0px 20px;
}

input[type='range']::-webkit-slider-thumb {
  -webkit-appearance: none;
  cursor: pointer;
  height: 12px;
  width: 12px;
  border-radius: 100%;
  border: 2px solid var(--button-highlight);
  background-color: var(--button-highlight);
  box-shadow: 0px 0px 20px 5px var(--button-highlight), 0px 0px 10px 2px var(--button-highlight);

  /* Corrects alignment isssue with chrome and edge */
  margin: -6px 0px;
}

input[type='range']::-moz-range-thumb {
  cursor: pointer;
  height: 10px;
  width: 10px;
  border-radius: 100%;
  border: 2px solid var(--button-highlight);
  background-color: var(--button-highlight);
  box-shadow: 0px 0px 20px 5px var(--button-highlight), 0px 0px 10px 2px var(--button-highlight);
}

input[type='range']::-moz-range-track {
  width: 100%;
  height: 2px;
  background: var(--font-highlight);
  box-shadow: 0px 0px 10px 2px var(--font-highlight);
  border-radius: 100%;
}

input[type='range']::-webkit-slider-runnable-track {
  width: 100%;
  height: 2px;
  background: var(--font-highlight);
  box-shadow: 0px 0px 10px 2px var(--font-highlight);
  border-radius: 100%;
}

input[type='range']:focus {
  outline: none;
}
